@import url(https://fonts.googleapis.com/css2?family=Cantarell:wght@700&display=swap);
.app {
  display: flex;
  align-items: center;
  font-family: "Cantarell", sans-serif;
}

.app.left {
  flex-direction: row;
}

.app.right {
  flex-direction: row-reverse;
}

.app.debugging {
  background-color: black;
}

.app .logo {
  z-index: 2;
}

.app .progress-bar.left {
  margin-left: -3rem;
  z-index: 1;
}

.app .progress-bar.right {
  margin-right: -3rem;
  z-index: 1;
}

.logo {
  height: 100px;
  width: 100px;
  background: rgb(26, 76, 109);
  background: linear-gradient(
    0,
    rgba(26, 76, 109, 1) 0%,
    rgba(64, 116, 194, 1) 100%
  );
  border: solid 0.5rem #7fd836;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo img {
  width: 90%;
  height: 90%;
  padding: 0.25rem 0.25rem 0 0;
}

.progress-container {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.progress-container.right {
  direction: rtl;
}

.progress-bar {
  position: relative;
  background: #1a4c6d;
  overflow: hidden;
  height: 40px;
  border-radius: 300px;
  border: solid 4px white;
  width: 500px;
}

.progress-bar.right {
  direction: rtl;
}

.progress-bar .completed {
  background: #26c2eb;
  height: 100%;
  text-align: right;
}

.progress-bar .completed {
  background-image: linear-gradient(
    -45deg,
    rgba(255, 255, 255, 0.17) 25%,
    transparent 25%,
    transparent 50%,
    rgba(255, 255, 255, 0.17) 50%,
    rgba(255, 255, 255, 0.17) 75%,
    transparent 75%
  );
  background-size: 20px 20px;
  overflow: hidden;
  -webkit-animation: progress-animation 1s ease-in-out;
          animation: progress-animation 1s ease-in-out;
}

.progress-text {
  position: absolute;
  display: flex;
  align-items: center;
  height: 100%;
  text-align: left;
  color: white;
  font-size: 2.3rem;
  z-index: 2;
  width: 100%;
}

.progress-text p {
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

.progress-text.right {
  direction: rtl;
}

@-webkit-keyframes progress-animation {
  0% {
    width: 0;
  }
}

@keyframes progress-animation {
  0% {
    width: 0;
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

